.card {
  display: flex;
  flex-wrap: wrap;
  padding-top: 27px;
  padding-bottom: 25px;
  justify-content: center;
  min-width: 1024px;
  max-width: 1200px;

  &__container {
    display: flex;
    flex-direction: column;
    background: #fff;
    border: 1px solid #f3f3f3;
    border-radius: 5px;
    max-width: 198px;
    margin: 23px 24px;
  }

  &_image {
    width: 160px;
    height: 134px;
    padding-top: 32px;
    padding-left: 19px;
  }

  &__tittle {
    font-style: normal;
    font-size: 12px;
    font-weight: 500;
    line-height: 18px;
    color: #060b35;
    padding-top: 40px;
    padding-left: 16px;
  }

  &__product-code {
    font-style: normal;
    font-size: 10px;
    font-weight: 400;
    line-height: 14px;
    color: #616070;
    padding-top: 4px;
    padding-left: 16px;
  }

  &__review-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding-top: 16px;
    padding-left: 16px;
    padding-right: 16px;
  }

  &__review {
    font-weight: 400;
    font-size: 10px;
    color: #060b35;
    box-sizing: border-box;
  }

  &__price {
    display: flex;
    justify-content: space-between;
    align-items: center;
    line-height: 18px;
    padding-top: 24px;
    padding-left: 16px;
    padding-right: 16px;
  }

  &__price-tittle {
    font-weight: 400;
    font-size: 12px;
    color: #616070;
  }

  &__price-item {
    font-weight: 700;
    font-size: 16px;
  }

  &__button {
    display: flex;
    align-items: center;
    justify-content: center;
    background: #00acdc;
    color: #fff;
    border-radius: 5px;
    width: 166px;
    height: 40px;
    margin: 16px;
    text-transform: uppercase;
    text-decoration: none;
    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
  }

  &__button:hover {
    border: 1px solid #00acdc;
    background: #fff;
    color: #00acdc;
    box-sizing: border-box;
    border-radius: 5px;
  }

  &__stars :nth-child(-n+4) {
    background: url(../images/star-active.svg) center no-repeat;
  }
}
